<template>
  <div class="">
     <rightStickIcon />
    <v-card class="mx-auto mt-n3">
      <v-img
        :src="
          require('../../views/attheairport/images/shopping/shoppingBanner.jpg')
        "
        class="my-3"
        contain
      />
      <v-card-title
        class="justify-center text-uppercase page-title"
        :class="{ 'page-titleSm': $vuetify.breakpoint.smAndDown }"
      >
        at the airport
      </v-card-title>
      <v-card-subtitle
        class="text-center page-sub-title"
        :class="{ 'page-subtitleSm': $vuetify.breakpoint.smAndDown }"
      >
        <h1 :class="$vuetify.breakpoint.smAndDown ? 'mt-5' : 'mt-0'">
          Shopping
        </h1>
      </v-card-subtitle>
      <v-container
        class="bg-surface-variant"
        :class="$vuetify.breakpoint.smAndDown ? 'mt-0' : 'mt-5'"
      >
        <!-- Food & Drink Start    -->
        <v-row
          no-gutters
          v-scrollanimation
          align="center"
          justify="center"
          :class="{ charmSmall: $vuetify.breakpoint.smAndDown }"
        >
          <v-col
            cols="12"
            sm="6"
            md="6"
            :class="[$vuetify.breakpoint.smAndDown ? 'order-2' : 'order-1']"
          >
            <!-- <v-carousel height="400" delimiter-icon="mdi-circle" :show-arrows="false" class="foodanddrinkcarousel">
                            <v-carousel-item v-for="(slide, i) in slides" :key="i">
                                <v-sheet height="100%">
                                    <div class="d-flex fill-height carouselText mt-5">
                                        <div :class="{'carouselText-Two': $vuetify.breakpoint.smAndDown}">
                                            <h2 class="pr-15"> {{ slide.title }} </h2>
                                            <p class="pr-15"> {{ slide.detail }} </p>
                                            <v-card-actions>
                                                <v-btn rounded dark class="gradient-button text-capitalize mb-2" style="padding:10px 20px !important" :class="$vuetify.breakpoint.smAndDown?'mt-0':'mt-2'">
                                                    More Info
                                                </v-btn>
                                            </v-card-actions>
                                        </div>
                                    </div>
                                </v-sheet>
                            </v-carousel-item>
                        </v-carousel> -->

            <v-card class="mx-auto" flat>
              <v-card-text>
                <div class="text-h5 text--primary">
                  <span>Duty-Free Shops </span>
                </div>
                <div class="text--primary mt-3">
                  There are duty-free shops at Chennai Airport selling a variety of luxury items, including cosmetics, fragrances, jewellery, watches, and liquor.
                </div>
              </v-card-text>
            </v-card>
            <v-card flat>
              <v-card-text>
                <div class="text-h5 text--primary">
                  <span>Book Stores </span>
                </div>
                <div class="text--primary mt-3">
                 There are bookstores at the airport where passengers can buy books, magazines, and newspapers.
                </div>
              </v-card-text>
            </v-card>

            <v-card flat>
              <v-card-text>
                <div class="text-h5 text--primary">
                  <span>Electronics and Gadgets </span>
                </div>
                <div class="text--primary mt-3">
                  Passengers can purchase a wide range of electronics and gadgets at the stores in terminals.
                </div>
              </v-card-text>
            </v-card>

            <v-card flat>
              <v-card-text>
                <div class="text-h5 text--primary">
                  <span>Clothing and Accessories </span>
                </div>
                <div class="text--primary mt-3">
                  Chennai Airport has several stores selling clothing and accessories, including top brands.
                </div>
              </v-card-text>
            </v-card>

            <v-card flat>
              <v-card-text>
                <div class="text-h5 text--primary">
                  <span>Souvenirs and Handicrafts </span>
                </div>
                <div class="text--primary mt-3">
                  Passengers can pick up local handicrafts, souvenirs, and gifts from the various handicraft stores

                </div>
              </v-card-text>
            </v-card>

            <v-card flat>
              <v-card-text>
                <div class="text-h5 text--primary">
                  <span>Convenience Stores </span>
                </div>
                <div class="text--primary mt-3">
             Convenience stores offer snacks, beverages, and travel essentials to passengers transiting.
                </div>
              </v-card-text>
            </v-card>

            <v-card flat>
              <v-card-text>
                <div class="text-h5 text--primary">
                  <span>Pharmacy</span>
                </div>
                <div class="text--primary mt-3">
                 Passengers can purchase medicines and other healthcare products at Apollo Pharmacy located inside the check in area (Domestic) and kerb area (T-2 International) of the airport.
                </div>
              </v-card-text>
            </v-card>
          </v-col>

          <v-col
            cols="12"
            sm="6"
            md="6"
            :class="[$vuetify.breakpoint.smAndDown ? 'order-1' : 'order-2']"
          >
            <v-hover>
              <v-card
                class="rounded-xl"
                style="box-shadow: 10px 10px 0 3px #e8effe; border-radius: 32px"
                :class="`${hover ? 'imgover' : 'imgleave'}`"
                slot-scope="{ hover }"
              >
                <v-img
                  class="rounded-xl"
                  src="../../views/attheairport/images/shopping/Ayurveda.jpg"
                  :class="$vuetify.breakpoint.smAndDown ? 'ml-0' : 'ml-15'"
                ></v-img>
              </v-card>
            </v-hover>
          </v-col>
        </v-row>
        <v-row
          no-gutters
          v-scrollanimation
          align="center"
          justify="center"
          :class="{ charmSmall: $vuetify.breakpoint.smAndDown }"
        >
          <v-col cols="12 mt-10">
            <v-card-title
              class="justify-center text-uppercase page-title mt-10"
              :class="{ 'page-titleSm': $vuetify.breakpoint.smAndDown }"
            >
              global Branded Shops
            </v-card-title>
            <v-card-subtitle
              class="text-center mt-2"
              :class="{ 'page-subtitleSm': $vuetify.breakpoint.smAndDown }"
            >
              <h1 :class="$vuetify.breakpoint.smAndDown ? 'mt-5' : 'mt-0'">
                Shopping Directory
              </h1>
            </v-card-subtitle>
          </v-col>
        </v-row>
        <v-card
          class="mx-auto rounded-xl mb-10 mt-10 searchBox"
          v-scrollanimation
          style="padding: 10px"
          :class="{ searchCard: $vuetify.breakpoint.lgAndUp }"
        >
          <v-row>
            <v-col cols="12" md="3">
              <v-list-item>
                <v-list-item-avatar rounded="0" size="20" class="mt-7 ml-5">
                  <v-img
                    :src="require('../../views/flights/images/search.svg')"
                  />
                </v-list-item-avatar>
                <v-list-item-content>
                  <div>
                    <v-text-field
                      :class="{
                        widthResponsive: $vuetify.breakpoint.smAndDown,
                      }"
                      v-model="search"
                      label="Search by name"
                      single-line
                      hide-details
                      v-on:keydown="filterName"
                    ></v-text-field>
                  </div>
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-divider class="mt-5 mb-5" vertical></v-divider>
            <v-col cols="12" md="3">
              <v-list-item two-line>
                <v-list-item-avatar rounded="0" size="20" class="mt-7 ml-5">
                  <v-img
                    :src="
                      require('../../views/attheairport/images/fooddrinkicon.svg')
                    "
                  />
                </v-list-item-avatar>
                <v-list-item-content>
                  <div>
                    <v-select
                      :items="allcuisines"
                      hide-details
                      label="All Cuisines"
                      flat
                      v-model="selectCuisines"
                      @change="filterByCuisines"
                    ></v-select>
                  </div>
                  <!-- <v-list-item-subtitle>or Departures</v-list-item-subtitle> -->
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-divider class="mt-5 mb-5" vertical></v-divider>
            <v-col cols="12" md="3">
              <v-list-item two-line>
                <v-list-item-avatar rounded="0" size="20" class="mt-7 ml-5">
                  <v-img
                    :src="
                      require('../../views/flights/images/allterminalIcon.svg')
                    "
                  />
                </v-list-item-avatar>
                <v-list-item-content>
                  <div>
                    <v-select
                      :items="allterminals"
                      hide-details
                      label="All Terminals"
                      flat
                      v-model="selectedTerminal"
                      @change="filterByTerminal"
                    ></v-select>
                  </div>
                  <!-- <v-list-item-subtitle>or Departures</v-list-item-subtitle> -->
                </v-list-item-content>
              </v-list-item>
            </v-col>

            <v-col cols="12" md="2">
              <v-list-item two-line>
                <v-list-item-content>
                  <v-btn rounded large color="#E8EFFE" @click="clearFilter">
                    Reset Filters
                  </v-btn>
                </v-list-item-content>
              </v-list-item>
            </v-col>
          </v-row>
        </v-card>
        <v-row
          dense
          v-scrollanimation
          align="center"
          :class="{ charmSmall: $vuetify.breakpoint.smAndDown }"
        >
          <v-col
            class="px-3"
            v-for="(brand, i) in branded"
            :key="i"
            cols="12"
            md="4"
            sm="6"
          >
            <v-card flat class="mx-auto ma-2" height="auto" width="100%">
              <div class="headingBox-txt">
                <h2>{{ brand.name }}</h2>
                <p>{{ brand.category }}</p>
              </div>
              <v-hover>
                <v-card
                  flat
                  :class="`${hover ? 'imgover' : 'imgleave'}`"
                  slot-scope="{ hover }"
                >
                  <v-img :src="brand.src" class="mt-3 mb-3 rounded-xl"></v-img>
                </v-card>
              </v-hover>
              <v-card-actions class="imageBox-txt">
                <v-card-title v-text="brand.location"></v-card-title>
                <v-spacer></v-spacer>
                <v-dialog transition="dialog-bottom-transition" max-width="800">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      rounded
                      dark
                      class="gradient-button pl-4 pr-4"
                      v-bind="attrs"
                      v-on="on"
                    >
                      More Info
                    </v-btn>
                  </template>
                  <template v-slot:default="dialog">
                    <v-card flat class="rounded-xl">
                      <v-row no-gutters align="center" justify="center">
                        <v-col>
                          <v-img
                            :src="brand.src"
                            class="mr-6 rounded-r-xl"
                          ></v-img>
                        </v-col>
                        <v-col>
                          <div class="text-uppercase page-title mt-10">
                            {{ brand.name }}
                          </div>
                          <div class="mt-2">
                            <h1>Shopping</h1>
                          </div>
                          <div class="mt-2">
                            <p>{{ brand.category }}</p>
                          </div>
                          <div>
                            {{ brand.detailcontent }}
                          </div>
                          <v-row class="mt-2">
                            <v-col cols="6">
                              <span style="display: block">Location</span>
                              <span>{{ brand.location }}</span>
                            </v-col>
                            <v-col cols="6">
                              <span style="display: block">Timings</span>
                              <span>{{ brand.timing }}</span>
                            </v-col>
                          </v-row>
                          <v-card-actions class="justify-end">
                            <v-btn
                              rounded
                              dark
                              large
                              class="gradient-button pl-10 pr-10"
                              @click="dialog.value = false"
                            >
                              Directions
                            </v-btn>
                            <v-btn
                              elevation="0"
                              icon
                              style="background-color: #e8effe"
                            >
                              <v-icon color="#0072FF"> mdi-phone </v-icon>
                            </v-btn>
                            <v-btn
                              elevation="0"
                              icon
                              style="background-color: #e8effe"
                            >
                              <v-icon color="#0072FF"> mdi-email </v-icon>
                            </v-btn>
                          </v-card-actions>
                        </v-col>
                      </v-row>
                    </v-card>
                  </template>
                </v-dialog>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </div>
</template>
<script>
import axios from "axios";
import rightStickIcon from "../../components/rightStickIcon"

export default {
  name: "shoppingAt",

  components: {
    rightStickIcon,
  },

  data() {
    return {
      search: "",
      allcuisines: [{ text: "All" }],

      allterminals: [{ text: "All" }],

      slides: [
        {
          title: "Pamper yourself with Kama Ayurveda Products",
          detail:
            "Looking for a last minute splurge? From luxury brands to  local handicrafts Chennai International Airport offers plenty of options for all those seeking retail therapy!",
        },
        {
          title: "Pamper yourself with Kama Ayurveda Products",
          detail:
            "Looking for a last minute splurge? From luxury brands to  local handicrafts Chennai International Airport offers plenty of options for all those seeking retail therapy!",
        },
      ],
      branded: [
        // {
        //   name: "Traditionz",
        //   category: "Jwellery",
        //   src: require("../../views/attheairport/images/shopping/Traditionz.jpg"),
        //   location: "T1 Departures",
        //   detailcontent:
        //     "A one-stop online solution for purchasing eyewear and its accessories. Shop Exclusive, Fashionable & affordable collection at Sunglass Hut.",
        //   timing: "Open 24 hours",
        // },
        // {
        //   name: "Samsonite",
        //   category: "Bags, Accessories",
        //   src: require("../../views/attheairport/images/shopping/Samsonite.jpg"),
        //   location: "T3 Arrivals",
        //   detailcontent:
        //     "A one-stop online solution for purchasing eyewear and its accessories. Shop Exclusive, Fashionable & affordable collection at Sunglass Hut.",
        //   timing: "Open 24 hours",
        // },
        // {
        //   name: "Sunglass hut",
        //   category: "Sunglasses, Accessories, Fashion",
        //   src: require("../../views/attheairport/images/shopping/sunglass.jpg"),
        //   location: "T3 Departures",
        //   detailcontent:
        //     "A one-stop online solution for purchasing eyewear and its accessories. Shop Exclusive, Fashionable & affordable collection at Sunglass Hut.",
        //   timing: "Open 24 hours",
        // },
      ],
      brandedData: [],
      cuisines: [],
      terminalData: [],
      selectedTerminal: "",
      selectCuisines: "",
    };
  },
  mounted() {
    console.log("Shopping");
    this.getPoiInfo();
  },
  methods: {
    getPoiInfo() {
      axios
        .post(process.env.VUE_APP_POI_URL + "poisroute/getpois", {
          appname: "maa",
          token:
            "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VyX2lkIjoiNTk1Zjc1NmY1NGVkYWYyMzA4MjYzMTlhIiwiaWF0IjoxNDk5ODc2OTM4LCJleHAiOjE1NTE3MTY5Mzh9.9vFu_xjGMNb_w_1i-qIuxI9pUkdAMFkRqvmHmJAByHk",
        })
        .then((poiData) => {
          console.log("poiData", poiData);
          if (poiData.data.success) {
            let data = poiData.data.data.filter(
              (element) => element.category == "Shopping"
            );

            data.forEach((element) => {
              if (this.allcuisines.indexOf(element.subcategory) == -1)
                this.allcuisines.push({ text: element.subcategory });

              if (this.allterminals.indexOf(element.terminal) == -1)
                this.allterminals.push({ text: element.terminal });
              this.allterminals.sort(
                (a, b) => parseFloat(a.text) - parseFloat(b.text)
              );

              this.branded.push({
                name: element.poi_multilingual["en"].name,
                cuisines: element.subcategory,
                src:
                  element.poi_url != ""
                    ? element.poi_url
                    : require("../../views/attheairport/images/dining/small-img/d1.jpg"),
                location: "T " + element.terminal + " " + element.sliceid,
                terminal: element.terminal,
              });
            });
            this.brandedData = this.branded;
            this.cuisines = this.allcuisines;
            this.terminalData = this.allterminals;

            this.selectedTerminal = this.allterminals[1].text;
            console.log("this.selectedTerminal ", this.selectedTerminal);
            this.filterByTerminal(this.selectedTerminal);
          }
        });
    },
    filterName() {
      console.log("filterName", this.search);
      this.allcuisines = [];
      this.allterminals = [];
      if (this.search.length) {
        this.branded = this.brandedData.filter((f) => {
          if (f.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1) {
            if (this.allcuisines.indexOf(f.cuisines) == -1)
              this.allcuisines.push({ text: f.cuisines });
            if (this.allterminals.indexOf(f.terminal) == -1)
              this.allterminals.push({ text: f.terminal });
            return f;
          }
        });
      } else {
        // this.branded = this.brandedData;
        // this.selectedTerminal = this.allterminals[1].text;
        this.filterByTerminal(this.selectedTerminal);
      }
    },
    filterByTerminal(terminal) {
      console.log("filterByTerminal", terminal);
      this.allcuisines = [];
      this.branded = this.brandedData.filter((f) => {
        if (f.terminal == terminal) {
          if (this.allcuisines.indexOf(f.cuisines) == -1)
            this.allcuisines.push({ text: f.cuisines });
          return f;
        }
      });
      console.log(" this.allcuisines", this.allcuisines);
    },
    filterByCuisines() {
      console.log("filterByCuisines ", this.selectCuisines);
      this.allterminals = [];
      this.branded = this.brandedData.filter((f) => {
        if (f.cuisines == this.selectCuisines) {
          if (this.allterminals.indexOf(f.terminal) == -1)
            this.allterminals.push({ text: f.terminal });
          return f;
        }
      });
    },
    clearFilter() {
      this.branded = this.brandedData;
      this.allcuisines = this.cuisines;
      this.allterminals = this.terminalData;
      this.selectedTerminal = this.allterminals[1].text;
      this.filterByTerminal(this.allterminals[1].text);
      this.selectCuisines = "";
      this.search = "";
    },
  },

  computed: {},
};
</script>
<style scoped>
.imageBox-txt.v-card__actions {
  padding: 0 !important;
}

.carouselText .v-card__actions {
  padding: 0;
}
.searchBox {
  max-width: 1050px;
  margin: 0 auto;
}

.searchBox .v-list-item {
  padding: 0 !important;
}
</style>